import { useState } from 'react';
import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { Card, Stack, Container } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { FormProvider, RHFTextField } from '../../components/hook-form';
// axios
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

export default function ChangePin() {
    const { themeStretch } = useSettings();
    const { enqueueSnackbar } = useSnackbar();
    const [isSubmitting, setIsSubmitting] = useState(false);
    const AccessToken = window.localStorage.getItem('accessToken');

    const ChangePinSchema = Yup.object().shape({
        currentPin: Yup.string().required('Current PIN is required').min(4, 'PIN must be at least 4 digits'),
        newPin: Yup.string().required('New PIN is required').min(4, 'PIN must be at least 4 digits'),
        confirmPin: Yup.string()
            .required('Please confirm your new PIN')
            .oneOf([Yup.ref('newPin'), null], 'PINs must match'),
    });

    const defaultValues = {
        currentPin: '',
        newPin: '',
        confirmPin: '',
    };

    const methods = useForm({
        resolver: yupResolver(ChangePinSchema),
        defaultValues,
    });

    const { reset, handleSubmit } = methods;

    const onSubmit = async (data) => {
        setIsSubmitting(true);
        try {
            await axios.post(`/api/change/pin/by/user/system/${AccessToken}/now`, {
                old_pin: data.currentPin,
                new_pin: data.newPin,
            });
            enqueueSnackbar('PIN changed successfully!', { variant: 'success' });
            reset();
        } catch (error) {
            enqueueSnackbar(error?.data?.message || 'Failed to change PIN', { variant: 'error' });
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Page title="Change PIN">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Change Transaction PIN"
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
                        { name: 'Change PIN' },
                    ]}
                />

                <Card sx={{ p: 3 }}>
                    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                        <Stack spacing={3}>
                            <RHFTextField
                                name="currentPin"
                                label="Current PIN"
                                type="password"
                                inputProps={{ maxLength: 4 }}
                            />

                            <RHFTextField
                                name="newPin"
                                label="New PIN"
                                type="password"
                                inputProps={{ maxLength: 4 }}
                            />

                            <RHFTextField
                                name="confirmPin"
                                label="Confirm New PIN"
                                type="password"
                                inputProps={{ maxLength: 4 }}
                            />

                            <LoadingButton
                                fullWidth
                                size="large"
                                type="submit"
                                variant="contained"
                                loading={isSubmitting}
                            >
                                Change PIN
                            </LoadingButton>
                        </Stack>
                    </FormProvider>
                </Card>
            </Container>
        </Page>
    );
}
